/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
.review-card {
  padding: 30px;
  background: #FFFFFF;
  border: 1px solid #F8F8F8;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
}
.review-card .header-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
}
.review-card .header-card .date {
  font-size: 18px;
  font-weight: 500;
  color: #767676;
}
.review-card .content-card .description {
  font-size: 18px;
  margin-bottom: 40px;
}
.review-card .content-card .username {
  font-weight: 700;
  font-size: 20px;
}

@media (max-width: 575px) {
  .review-card {
    padding: 20px;
  }
  .review-card .header-card {
    margin-bottom: 40px;
  }
  .review-card .header-card .date {
    font-size: 14px;
  }
  .review-card .header-card .stars svg {
    width: 18px;
    height: 18px;
  }
  .review-card .content-card .description {
    font-size: 16px;
    margin-bottom: 30px;
  }
  .review-card .content-card .username {
    font-size: 18px;
  }
}
.load-more-wrap {
  padding: 100px 0 60px;
  display: flex;
  justify-content: center;
}

.pagination-nav {
  display: flex;
  justify-content: center;
}
.pagination-nav .page-item[aria-current=page] .page-link {
  position: relative;
}
.pagination-nav .page-item[aria-current=page] .page-link::before {
  content: "";
  position: absolute;
  bottom: 3px;
  width: 10px;
  height: 1px;
  background-color: #767676;
}
.pagination-nav .page-item:first-child {
  margin-right: 10px;
}
.pagination-nav .page-item:first-child .page-link {
  background-color: #F5F5F5;
}
.pagination-nav .page-item:first-child .page-link:hover {
  background-color: #F5F5F5 !important;
}
.pagination-nav .page-item:last-child {
  margin-left: 10px;
}
.pagination-nav .page-item:last-child .page-link {
  background-color: #F5F5F5;
}
.pagination-nav .page-item:last-child .page-link:hover {
  background-color: #F5F5F5 !important;
}
.pagination-nav .page-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 30px;
  height: 30px;
  color: #767676;
  border: none;
  border-radius: 50% !important;
  transition: all 0.2s ease-in-out;
  font-weight: 500;
  background-color: transparent;
}
.pagination-nav .page-link:focus {
  box-shadow: none;
}
.pagination-nav .page-link svg {
  transition: all 0.2s ease-in-out;
  fill: #767676;
}
.pagination-nav .page-item:not([aria-current=page]) .page-link:hover {
  color: #232323;
  background-color: #FFCC00;
}
.pagination-nav .page-item:not([aria-current=page]) .page-link:hover svg {
  fill: #232323;
}

@media (max-width: 575px) {
  .load-more-wrap {
    padding: 60px 0 40px;
  }

  .pagination-nav .pagination {
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    padding: 0 40px;
  }
  .pagination-nav .page-item:last-child, .pagination-nav .page-item:first-child {
    position: absolute;
  }
  .pagination-nav .page-item:first-child {
    left: 0;
  }
  .pagination-nav .page-item:last-child {
    right: 0;
  }
}
/* Section */
.dignity-section .dignity-content-wrap {
  padding: 30px 0;
}
.dignity-section .dignity-warp {
  position: relative;
  display: flex;
}
.dignity-section .dignity-warp img {
  margin-right: 15px;
}
.dignity-section .dignity-warp p {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
}
.dignity-section .dignity-warp .circle {
  position: absolute;
  width: 213px;
  height: 213px;
  background: linear-gradient(83.16deg, #FFE26C 74.08%, rgba(255, 226, 108, 0) 90.42%);
  border-radius: 50%;
  z-index: -1;
}
.dignity-section .dignity-col:nth-child(1) .circle {
  top: -25px;
  left: 115px;
}
.dignity-section .dignity-col:nth-child(2) .circle {
  top: -34px;
  left: 34px;
}
.dignity-section .dignity-col:nth-child(3) .circle {
  top: -20px;
  left: 60px;
}

.number-animation {
  transition: all 0.2s ease-in-out;
}

.number-animation.hidden {
  opacity: 0;
}

.number-animation.show {
  opacity: 1;
}

@media (min-width: 1450px) {
  .dignity-section .dignity-content {
    padding: 0 15px;
    margin-left: -170px;
    margin-right: -170px;
  }
}
@media (max-width: 1449px) {
  .dignity-section .dignity-warp .h1 {
    font-size: 40px;
  }
  .dignity-section .dignity-warp p {
    font-size: 20px;
  }
}
@media (max-width: 1199px) {
  .dignity-section .dignity-content-wrap {
    padding: 0;
  }
  .dignity-section .dignity-warp .circle {
    height: 160px;
    width: 160px;
  }
  .dignity-section .dignity-col:nth-child(1) img {
    width: 150px;
  }
  .dignity-section .dignity-col:nth-child(1) .circle {
    top: -32px;
    left: 90px;
  }
  .dignity-section .dignity-col:nth-child(2) img {
    width: 130px;
  }
  .dignity-section .dignity-col:nth-child(2) .circle {
    top: -20px;
    left: 25px;
  }
  .dignity-section .dignity-col:nth-child(3) .circle {
    top: -25px;
    left: 40px;
  }
  .dignity-section .dignity-col:nth-child(3) img {
    width: 140px;
  }
}
@media (max-width: 991px) {
  .dignity-section .row {
    margin: -10px;
  }
  .dignity-section .dignity-col {
    padding: 10px;
  }
  .dignity-section .dignity-warp {
    align-items: center;
    padding: 30px 20px;
    border-radius: 8px;
    background-color: #FFE26C;
    height: 100%;
  }
  .dignity-section .dignity-warp img {
    margin-right: 0;
    max-width: 100%;
    height: auto;
  }
  .dignity-section .dignity-warp .description {
    min-width: 125px;
    margin-left: 20px;
  }
  .dignity-section .dignity-warp .circle {
    display: none;
  }
  .dignity-section .dignity-warp .h1 {
    font-size: 36px;
  }
  .dignity-section .dignity-warp p {
    font-size: 18px;
  }
}
/* Form */
.review-form .form-title {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
}
.review-form .form-subtitle {
  color: #767676;
  margin-bottom: 30px;
}
.review-form .rating-group {
  margin: 0 -5px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
}
.review-form .rating-group .rating-label {
  padding: 0 5px;
  cursor: pointer;
}
.review-form .rating-group .rating-label svg {
  fill: none;
  stroke: #989898;
}
.review-form .rating-group .rating-input {
  display: none;
}
.review-form .rating-group > input:checked ~ label svg,
.review-form .rating-group:not(:checked) > label:hover svg,
.review-form .rating-group:not(:checked) > label:hover ~ label svg {
  fill: #FFCC00;
  stroke: #FFCC00;
}
.review-form .rating-group > input:checked + label:hover svg,
.review-form .rating-group > input:checked ~ label:hover svg,
.review-form .rating-group > label:hover ~ input:checked ~ label svg,
.review-form .rating-group > input:checked ~ label:hover ~ label svg {
  fill: #FFE26C;
  stroke: #FFE26C;
}
.review-form .from-group-wrap {
  max-width: 350px;
}
.review-form .checkbox-block {
  margin-bottom: 30px;
}
.review-form .checkbox-block .custom-control-label {
  line-height: 2;
}
.review-form .checkbox-block .custom-control-label::after {
  top: 5px;
}
.review-form .checkbox-block .custom-control-label::before {
  top: calc(5px + 4px);
}
.review-form button[type=submit] {
  height: 48px;
  width: 100%;
  font-weight: 600;
}

@media (max-width: 575px) {
  .review-form .form-title {
    font-size: 36px;
  }
}
.reviews-hero-section .section-title {
  margin-bottom: 50px;
}
.reviews-hero-section .widget .XYyMK {
  margin: 0;
  position: absolute;
  z-index: 1;
}
.reviews-hero-section .widget .eapps-widget-toolbar {
  display: none;
}

.reviews-section .container {
  position: relative;
}
.reviews-section .container::before {
  width: 1453px;
  height: 1089px;
  content: "";
  position: absolute;
  right: -590px;
  top: 220px;
  background-image: url(../../assets/img/svg/picture/10.svg);
}
.reviews-section .row {
  margin: -10px;
}
.reviews-section .review-col {
  padding: 10px;
}

@media (max-width: 1199px) {
  .reviews-section .container::before {
    display: none;
  }
}
@media (max-width: 575px) {
  .reviews-hero-section .widget .XYyMK {
    position: relative;
    margin-top: 40px;
  }
}